import { Component, OnInit } from '@angular/core';
import { MenuItem } from 'primeng/api';
import { SitesServiceService } from '../services/sites-service.service';
import { ActivatedRoute } from '@angular/router';


@Component({
  selector: 'quadlogic-site',
  templateUrl: './site.component.html',
  styleUrls: ['./site.component.css']
})
export class SiteComponent implements OnInit {

  id: number;
  site: any = {};
  private sub: any;
  items: any[];


  constructor(private siteService: SitesServiceService, private route: ActivatedRoute) { }

  ngOnInit() {
    this.sub = this.route.params.subscribe(params => {
      this.id = +params['id']; // (+) converts string 'id' to a number      
   });

    this.siteService.getSiteDetails(this.id)
    .subscribe(
      (data)=>{
        this.site = data.site;
        // localStorage.setItem('site_id',this.site.id);
      }
    )
    this.items = [
      { label: 'Overview' },
      { label: 'Sections' },
      { label: 'Units' },
      { label: 'Meters' },
      { label: 'Tenants' }
    ];
  }

}



