import { Component, OnInit, TemplateRef, ViewChild, NgZone } from '@angular/core';
import { Site } from '../../sites/site';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl, ValidationErrors } from '@angular/forms';
// sites service
import { SitesServiceService } from '../../sites/services/sites-service.service';
import 'rxjs/Rx';
import { ValidatePassword, matchPassword, emailAvailability, phoneNumberAvailability } from './../../shared/common.validators';


@Component({
  selector: 'quadlogic-sites-list',
  templateUrl: './sites-list.component.html',
  styleUrls: ['./sites-list.component.css']
})

// interface for demo

export class SitesListComponent implements OnInit{
  siteCreateForm: FormGroup;
  sites: any[];
  display:boolean = false;
  constructor(private siteService: SitesServiceService,
    private _fb: FormBuilder) {
      this.siteCreateForm = this._fb.group({
        name: new FormControl(),
        address_line1: new FormControl(null, Validators.required),
        address_line2: new FormControl(null, Validators.required),
        city: new FormControl(null, Validators.required),
        state: new FormControl(null, Validators.required),
        country: new FormControl(null, Validators.required),
        zipcode: new FormControl(null, Validators.required),
        short_zip_code: new FormControl(null, Validators.required)
      });
  }
  
  ngOnInit() {
    this.siteService.getSites()
    .subscribe(
      (data)=>{
        console.log(data);
        this.sites = data.sites;
      }
    )
  }
  
  createSite(siteCreateForm){
    if(siteCreateForm.valid){ 
      this.siteService.createSite(siteCreateForm.value).subscribe((res:any)=>{
        console.log(res);
        this.sites.unshift(res.site);
        this.display = false;
        this.siteCreateForm.reset();
      })
    }
    else{
      this.setFormTouched(this.siteCreateForm);  
    }
  }


  // function for validate all form fields
  setFormTouched(form_obj: any) {
    // this._toastr.error("please fill all * marked fields", 'Oops!', { showCloseButton: true });
    Object.keys(form_obj.controls).forEach(field => {
      const control = form_obj.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }
  
  showDialog(){
    this.display = true;
  }

}



